  getData = async () => {
    //https://api.myjson.com/bins/1gnkhc
    //https://api.myjson.com/bins/m7848
    const response = await fetch("https://api.myjson.com/bins/1gnkhc");
    const json = await response.json();
    console.log("JSON ==>", json);
    return json;
  };

  placeSelectedHandle = (latLng, placeName) => {
    this.setState({ searchedPlace: latLng, searchedPlaceName: placeName });
  };

  mouseCoordinates = m => {
    let x = m.latLng.lat();
    let y = m.latLng.lng();

    this.setState({ currentPosition: `(${x}, ${y})` });
  };
  getDistance = () => {
    // seconds between when the component loaded and now
    const differentInTime = (new Date() - this.initialDate) / 1000; // pass to seconds
    return differentInTime * this.velocity; // d = v*t -- Based on Newtons Law
  };

  componentDidMount = () => {
    this.state.path.forEach((path, index) => {
      this.interval.push(setInterval(() => this.moveObject(index), 1000));
    });
  };

  componentWillUnmount = () => {
    this.interval.forEach(inttttt => clearInterval(inttttt));
  };

  moveObject = index => {
    const distance = this.getDistance();
    console.log("yanal", distance);

    if (!distance) {
      return;
    }

    let progress = this.state.path[index].filter(
      coordinates => coordinates.distance < distance
    );
    const nextLine = this.state.path[index].find(
      coordinates => coordinates.distance > distance
    );
    console.log("a", index, nextLine);

    if (!nextLine) {
      const stateProgress = [...this.state.progress];
      stateProgress[index] = progress;
      this.setState(prevState => ({
        progress: stateProgress
      }));
      return; // it's the end!
    }

    const lastLine = progress[progress.length - 1];

    const lastLineLatLng = new window.google.maps.LatLng(
      lastLine.lat,
      lastLine.lng
    );

    const nextLineLatLng = new window.google.maps.LatLng(
      nextLine.lat,
      nextLine.lng
    );

    // distance of this line
    const totalDistance = nextLine.distance - lastLine.distance;
    const percentage = (distance - lastLine.distance) / totalDistance;

    const position = window.google.maps.geometry.spherical.interpolate(
      lastLineLatLng,
      nextLineLatLng,
      percentage
    );

    progress = progress.concat(position);
    const stateProgress = [...this.state.progress];
    stateProgress[index] = progress;
    this.setState(prevState => ({
      progress: stateProgress
    }));
  };

  componentWillMount = () => {
    const data = this.getData().then(json => {

      json.forEach((path, inndex) => {
        console.log(json);
        json[inndex] = path.map((coordinates, i, array) => {
          if (i === 0) {
            return { ...coordinates, distance: 0 }; // starting location
          }
          const { lat: lat1, lng: lng1 } = coordinates;
          const latLong1 = new window.google.maps.LatLng(lat1, lng1);
  
          const { lat: lat2, lng: lng2 } = array[0];
          const latLong2 = new window.google.maps.LatLng(lat2, lng2);
  
          // in meters:
          const distance = window.google.maps.geometry.spherical.computeDistanceBetween(
            latLong1,
            latLong2
          );
  
          return { ...coordinates, distance };
        });

        this.setState({ path: json })
  
        // console.log(path);
      });
    });
    console.log("willMOUNT", data);
    console.log("this.state.path", this.state.path);
  };

  componentDidUpdate = () => {
    const distance = this.getDistance();
    if (!distance) {
      return;
    }

    this.state.path.forEach((path, i) => {
      let progress = path.filter(
        coordinates => coordinates.distance < distance
      );
      const nextLine = path.find(
        coordinates => coordinates.distance > distance
      );
      // console.log("x",path,nextLine)

      let point1, point2;

      if (nextLine) {
        point1 = progress[progress.length - 1];
        point2 = nextLine;
      } else {
        // it's the end, so use the latest 2
        point1 = progress[progress.length - 2];
        point2 = progress[progress.length - 1];
      }

      const point1LatLng = new window.google.maps.LatLng(
        point1.lat,
        point1.lng
      );
      const point2LatLng = new window.google.maps.LatLng(
        point2.lat,
        point2.lng
      );

      const angle = window.google.maps.geometry.spherical.computeHeading(
        point1LatLng,
        point2LatLng
      );
      const actualAngle = angle - 90;

      this.degrees[i] = actualAngle;

      let airplanesButton = document.getElementById("airplanesOnly");
      airplanesButton.addEventListener("click", () => {
        this.state.selectedLayerAirplane = true;
        this.state.selectedLayerHybrid = false;
        this.state.selectedLayerLand = false;
        this.state.selectedLayerVessels = false;
        //console.log(this.state.selectedLayerAirplane);
      });

      let hybridButton = document.getElementById("Hybrid");
      hybridButton.addEventListener("click", () => {
        this.state.selectedLayerHybrid = true;
        this.state.selectedLayerAirplane = false;
        this.state.selectedLayerVessels = false;
        this.state.selectedLayerLand = false;
      });

      let landVehiclesButton = document.getElementById("Land");
      landVehiclesButton.addEventListener("click", () => {
        this.state.selectedLayerHybrid = false;
        this.state.selectedLayerAirplane = false;
        this.state.selectedLayerVessels = false;
        this.state.selectedLayerLand = true;
      });

      let vesslesButton = document.getElementById("Vessels");
      vesslesButton.addEventListener("click", () => {
        this.state.selectedLayerHybrid = false;
        this.state.selectedLayerAirplane = false;
        this.state.selectedLayerVessels = true;
        this.state.selectedLayerLand = false;
      });
    });
  };

