import React from "react";
import PlacesAutocomplete, {
    geocodeByAddress,
    getLatLng
} from "react-places-autocomplete";

export default function SearchBar(props) {
    	
    const [address, setAddress] = React.useState("");
    const [coordinates, setCoordinates] = React.useState(
        {lat: null, lng: null}
        );

    const handleSelect = async value => {
        const results = await geocodeByAddress(value);
        const latLng = await getLatLng(results[0]);
        await setAddress(value);
        await setCoordinates(latLng);
        const AddressName = results[0].formatted_address;
        props.onPlaceSelected(latLng, AddressName);
        // console.log(results);
        // console.log("Places Coordinates ====> ", coordinates);
    };
    
    return (
        <div>

            <PlacesAutocomplete 
                value={address} 
                onChange={setAddress} 
                onSelect={handleSelect}
            >
            {({getInputProps, suggestions, getSuggestionItemProps, loading}) => (
                
                <div class="SearchBar">
                    {/* <p>lattitude : {coordinates.lat}</p>
                    <p>longitude: {coordinates.lng}</p> */}
                    <input id="searchbarInput" {...getInputProps({placeholder: "Search the Map 🔍"})}/>
                    <div>
                        {loading ? <div>...loading</div> : null}

                        {suggestions.map((suggestion) => {
                            const style = {
                                backgroundColor : suggestion.active ? "#41b6e6" : "#fff"
                            };


                            return (
                                <div class="suggestionsbox" {...getSuggestionItemProps(suggestion, {style})}>{suggestion.description}
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}
            </PlacesAutocomplete>
         </div>
    )
}
